export const GET_LEDGER_ACCOUNTS_PROCESS_STATUS = gql`
  query GET_LEDGER_ACCOUNTS_PROCESS_STATUS(
    $processId: ID
    $clientNumber: [ID]
  ) {
    myAccountingFirms {
      accountingFirmNumber
      companyNumber
      clients(clientNumber: $clientNumber) {
        clientNumber
        runningProcesses(processId: $processId) {
          processId
          processName
          processModule
          processStarted
          processFinished
          processPercentCompleted
          processStatus
          errors {
            itemId
            field
            fieldValue
            errorCode
            errorType
            errorMessage
          }
        }
      }
    }
  }
`

