export const GET_BALANCE_SHEET = gql`
  query(
    $clientNumber: [ID]
    $periodId: Int!
    $showZeroBalanceAccounts: Boolean!
    $showPercentages: Boolean
    $assumeLastAnnualPeriodIsClosed: Boolean
  ) {
    myAccountingFirms {
      clients(clientNumber: $clientNumber) {
        accounting {
          reports {
            balanceSheet(
              periodId: $periodId
              showZeroBalanceAccounts: $showZeroBalanceAccounts
              showPercentages: $showPercentages
              assumeLastAnnualPeriodIsClosed: $assumeLastAnnualPeriodIsClosed
            ) {
              assets {
                currentAssets {
                  ledgerAccountId
                  name
                  nature
                  finalBalance
                  percentage
                }
                nonCurrentAssets {
                  ledgerAccountId
                  name
                  nature
                  finalBalance
                  percentage
                }
                deferredAssets {
                  ledgerAccountId
                  name
                  nature
                  finalBalance
                  percentage
                }
                totalCurrentAssets
                percentageCurrentAssets
                totalNonCurrentAssets
                percentageNonCurrentAssets
                totalDeferredAssets
                percentageDeferredAssets
                totalAssets
              }
              liabilities {
                currentLiabilities {
                  ledgerAccountId
                  name
                  nature
                  finalBalance
                  percentage
                }
                nonCurrentLiabilities {
                  ledgerAccountId
                  name
                  nature
                  finalBalance
                  percentage
                }
                deferredLiabilities {
                  ledgerAccountId
                  name
                  nature
                  finalBalance
                  percentage
                }
                totalCurrentLiabilities
                percentageCurrentLiabilities
                totalNonCurrentLiabilities
                percentageNonCurrentLiabilities
                totalDeferredLiabilities
                percentageDeferredLiabilities
                totalLiabilities
                percentageLiabilitiesPlusCapital
              }
              capital {
                capital {
                  ledgerAccountId
                  name
                  nature
                  finalBalance
                  percentage
                }
                totalCapital
                percentageLiabilitiesPlusCapital
              }
              totalLiabilitiesPlusCapital
            }
          }
        }
      }
    }
  }
`

