<script>
import GxLoader from '@components/_Core/Utils/GxLoader'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'

export default {
  name: 'GxUploadFile',
  components: {
    GxLoader,
    GxCardModal,
  },
  props: {
    fileType: {
      type: String,
      required: false,
      default: 'archivo',
    },
    extensions: {
      type: Array,
      default: () => {
        return []
      },
    },
    fileProp: {
      type: Object,
      required: false,
    },
    fileNameProp: {
      type: String,
      required: false,
      default: '',
    },
    fileBase64Prop: {
      type: String,
      required: false,
      default: '',
    },
    fileDownload: {
      type: String,
      required: false,
      default: null,
    },
  },
  data() {
    return {
      file: this.fileProp,
      fileName: this.fileNameProp,
      fileBase64: this.fileBase64Prop,
      loadingFile: false,
      errorDescription: '',
    }
  },
  computed: {
    getExtensionsList() {
      return this.extensions.join(',')
    },
  },
  methods: {
    async downloadTemplate() {
      console.log('Descargar XML')
      window.location.assign(this.fileDownload)
    },
    async addFileButton(event) {
      this.file = event.target.files[0]
      this.getBase64()
    },
    addDragFile(e) {
      this.file = e.dataTransfer.files[0]
      this.getBase64()
    },
    setFile(file) {
      this.file = file
      this.getBase64()
    },
    getBase64() {
      this.fileName = this.file.name
      if (this.validateExtensions()) {
        this.loadingFile = true
        let reader = new FileReader()
        reader.onloadend = () => {
          // Since it contains the Data URI, we should remove the prefix and keep only Base64 string
          this.fileBase64 = reader.result.replace(/^data:.+;base64,/, '')
          //console.log(this.file)
          this.$emit('sendFile', this.fileBase64)
        }
        reader.readAsDataURL(this.file)
      }
    },
    validateExtensions() {
      try {
        let lastDot = this.fileName.lastIndexOf('.')
        let ext = this.fileName.substring(lastDot)
        if (this.extensions.indexOf(ext) > -1) {
          return true
        } else {
          this.errorDescription = 'La extensión no es permitida'
          return false
        }
      } catch (EX) {
        //console.log('catch')
        return false
      }
    },
    removeFile() {
      this.file = null
      this.fileName = ''
      this.fileBase64 = ''
      this.loadingFile = false
      this.errorDescription = ''
    },
    // onSuccessModalClose() {
    //   console.log('ERROR')
    // },
  },
}
</script>
<template>
  <div
    class="flex flex-col items-center content-center h-64 border-2 border-gray-400 rounded-lg w-66 de:w-88 de:h-70"
    :class="{
      'border-dashed hover:border-primary hover:bg-gray-200': !loadingFile,
      'border-solif': loadingFile,
    }"
  >
    <div
      v-show="!loadingFile"
      class="flex flex-col items-center content-center cursor-pointer"
      @click="$refs.uploadFile.click()"
      v-cloak
      @drop.prevent="addDragFile"
      @dragover.prevent
    >
      <div class="h-44">
        <i
          class="content-center mt-6 text-gray-400 text-10xl material-icons-round"
          >cloud_upload</i
        >
      </div>
      <input
        type="file"
        class="hidden"
        id="uploadFile"
        ref="uploadFile"
        @change="addFileButton($event)"
        :accept="getExtensionsList"
      />
      <div class="flex flex-col items-center text-center content-center">
        <p v-if="!this.fileDownload" class="text-base font-black de:text-2xl"
          >Arrastra tu {{ fileType }} aquí</p
        >
        <!-- :href="fileDownload" -->
        <!--  -->
        <p v-else class="text-base font-black de:text-xl"
          ><span
            @click.prevent.stop="downloadTemplate"
            class="underline font-black hover:text-gray-600 hover:text-gray-700 hover:no-underline"
            >Descargar la plantilla aquí.</span
          ><br />
          y cuando esté lista arrástrala a este recuadro
        </p>

        <p class="mt-1 text-xs font-bold de:text-base"
          >O
          <span
            class="font-bold underline hover:font-black text-secondary hover:no-underline"
            >selecciónalo</span
          >
          de tu equipo</p
        >
        <p class="text-2xs de:text-xs" v-if="extensions.length > 0">
          Archivos con extensión
          <span v-for="(extension, index) in extensions" :key="index"
            >{{ extension }}
          </span>
        </p>
      </div>
    </div>
    <div v-show="loadingFile">
      <GxLoader class="mt-20 mb-10" />
      <span class="flex w-full text-xs de:text-sm">{{ this.fileName }}</span>
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Error en el formato del archivo'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="removeFile">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </div>
</template>
