<script>
import GxLoader from '@components/_Core/Utils/GxLoader'
import GxProgress from '@components/_CoreTaxes/Progress/GxProgress'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
import { axiosGxYakBackMigrations } from '@src/axiosConfig'
export default {
  name: 'ProgressMigrationBackup',
  data() {
    return {
      porcentaje: 1,
      currentMigratedItem: '',
      migrationId: null,
      companyNumber: null,
      taxId: null,
      postMigrationContinue: null,
      errorDescription: '',
      numberOfLedgerAccounts: 0,
      numberOfJournalEntries: 0,
      numberOfPeriods: 0,
      numberOfSuppliers: 0,
      finished: false,
    }
  },
  components: {
    GxLoader,
    GxProgress,
    GxCardModal,
  },
  mounted() {
    this.migrationId = localStorage.getItem('migrationId')
    this.companyNumber = localStorage.getItem('companyNumber')
    this.taxId = localStorage.getItem('migration_tax_id')

    this.postMigrationContinue = {
      migrationId: this.migrationId,
      companyNumber: Number(this.companyNumber),
      taxId: this.taxId,
    }
    //console.log(this.postMigrationContinue)
    this.getStatusLoad()
  },
  methods: {
    startMigration() {
      //console.log(this.postMigrationContinue)
      axiosGxYakBackMigrations
        .post(
          `/api/v1/migrations/` + this.migrationId + '/continue_migration',
          this.postMigrationContinue
        )
        .then((res) => {
          this.loading = false
          this.getStatus()
        })
        .catch((err) => {
          this.loading = false
          this.errorDescription = this.$utils.parseServerError(err)
          //console.log(this.errorDescription)
        })
    },
    getStatusLoad() {
      axiosGxYakBackMigrations
        .get(`/api/v1/migrations/` + this.migrationId)
        .then((res) => {
          let informationProgress = res.data
          if (
            informationProgress.migrationStatus === 'waitingForUserResponse' &&
            informationProgress.subStage === 'mounted'
          ) {
            //console.log('waitingForUserResponse')

            this.$refs.gxProgress.changeProgress(this.porcentaje)
            //console.log(this.migrationId)
            this.startMigration()
          } else {
            if (informationProgress.migrationStatus === 'finishedWithErrors') {
              //console.log('finishedWithErrors')

              this.errorDescription =
                informationProgress.errors.length > 0
                  ? informationProgress.errors[0]
                  : 'Se presentaron algunos errores al migrar tu información'
            } else {
              if (
                informationProgress.migrationStatus ===
                  'waitingForUserResponse' &&
                informationProgress.subStage === 'mounted'
              ) {
                this.getSummary()
              } else {
                //console.log('inprogress')
                this.getStatus()
              }
            }
          }
        })
        .catch((err) => {
          //this.loading = false
          this.errorDescription = this.$utils.parseServerError(err)
          //console.log(this.errorDescription)
        })
    },
    getStatus() {
      axiosGxYakBackMigrations
        .get(`/api/v1/migrations/` + this.migrationId)
        .then((res) => {
          let informationProgress = res.data
          // console.log('exito')
          // console.log(informationProgress)
          // console.log(informationProgress.status)
          this.subStageProgress(
            informationProgress.subStage,
            informationProgress.percentCompleted
          )
          this.migrationProgress(
            informationProgress.migrationStatus,
            informationProgress.errors
          )
        })
        .catch((err) => {
          //this.loading = false
          this.errorDescription = this.$utils.parseServerError(err)
          //console.log(this.errorDescription)
        })
    },
    goToDashboard() {
      this.clearItemsLocalStorage()
      this.$router.push({ name: 'signupAccountCompleted' })
    },
    getSummary() {
      axiosGxYakBackMigrations
        .get(`/api/v1/migrations/` + this.migrationId + '/migration_summary')
        .then((res) => {
          let summary = res.data
          this.finished = true
          // console.log('exito')
          // console.log(summary)
          for (let i = 0; i < summary.length; i++) {
            switch (summary[i].stage) {
              case 'ledgerAccounts':
                this.numberOfLedgerAccounts = summary[i].numberElements
                break
              case 'journalEntries':
                this.numberOfJournalEntries = summary[i].numberElements
                break
              case 'periods':
                this.numberOfPeriods = summary[i].numberElements
                break
              case 'suppliers':
                this.numberOfSuppliers = summary[i].numberElements
                break
            }
          }
        })
        .catch((err) => {
          //this.loading = false
          this.errorDescription = this.$utils.parseServerError(err)
          //console.log(this.errorDescription)
        })
    },
    migrationProgress(migrationStatus, errors) {
      let timeout = null
      switch (migrationStatus) {
        case 'inProgress':
          timeout = setTimeout(() => {
            this.getStatus()
          }, 1000)

          this.$once('hook:beforeDestroy', () => {
            clearTimeout(timeout)
          })
          break
        case 'finishedWithErrors':
          this.errorDescription =
            errors.length > 0
              ? errors[0]
              : 'Se presentaron algunos errores al migrar tu información'
          break
      }
    },
    subStageProgress(subStage, percentCompleted) {
      switch (subStage) {
        case 'getLedgerAccounts':
        case 'analyzeLedgerAccounts':
          this.notificationProgress(3, 'Recuperando catálogo de cuentas')
          break
        case 'confirmLedgerAccounts':
          this.notificationProgress(5, 'Analizando catálogo de cuentas')
          break
        case 'saveLedgerAccounts':
          this.notificationProgress(
            5 + (percentCompleted / 100) * 20,
            'Guardando el catálogo de cuentas'
          )
          break
        case 'getPeriods':
          this.notificationProgress(
            25 + (percentCompleted / 100) * 5,
            'Recuperando los periodos'
          )
          break
        case 'savePeriods':
          this.notificationProgress(
            30 + (percentCompleted / 100) * 5,
            'Guardando los periodos'
          )
          break
        case 'getJournalEntries':
          this.notificationProgress(
            35 + (percentCompleted / 100) * 5,
            'Recuperando el listado de pólizas'
          )
          break
        case 'saveJournalEntries':
          this.notificationProgress(
            40 + (percentCompleted / 100) * 35,
            'Guardando  el listado de pólizas'
          )
          break
        case 'getBeginningBalance':
          this.notificationProgress(
            75 + (percentCompleted / 100) * 10,
            'Recuperando saldos iniciales'
          )
          break
        case 'saveBeginningBalance':
          this.notificationProgress(
            85 + (percentCompleted / 100) * 5,
            'Guardando los saldos iniciales'
          )
          break
        case 'getSuppliers':
          this.notificationProgress(
            90 + (percentCompleted / 100) * 3,
            'Recuperando el listado de proveedores'
          )
          break
        case 'saveSuppliers':
          this.notificationProgress(
            93 + (percentCompleted / 100) * 5,
            'Guardando  el listado de proveedores'
          )
          break
        case 'dismounting':
          this.notificationProgress(99, 'Quitando archivos temporales')
          break
        case 'dismounted':
          this.notificationProgress(100, 'Finalizando')
          this.getSummary()
          break
      }
    },
    notificationProgress(porcen, item) {
      this.porcentaje = porcen
      this.currentMigratedItem = item
      if (this.$refs.gxProgress) {
        this.$refs.gxProgress.changeProgressAndElement(
          this.porcentaje,
          this.currentMigratedItem
        )
      }
    },
    goToBack() {
      this.$router.push({ name: 'importAccountingBackup' })
    },
    resetProcess() {
      this.$router.push({ name: 'createNewAccountingClientView' })
    },
    clearItemsLocalStorage() {
      localStorage.removeItem('migration_tax_id')
      localStorage.removeItem('migration_business_name')
      localStorage.removeItem('configurationType')
    },
  },
}
</script>
<template>
  <div
    class="flex flex-col items-center flex-grow de:p-0 body-gradients font-nunito"
  >
    <div class="flex flex-col">
      <div class="mx-auto mt-0 de:mt-8">
        <div
          class="flex flex-col p-8 mt-0 bg-white rounded-lg de:mt-40 w-82 shadow-3 h-86 z-1 de:w-146"
        >
          <div v-if="!this.finished" class="flex flex-col text-center"
            ><div v-if="$mq === 'de'" class="flex-grow text-2xl">
              <span class="font-black"
                >Estamos importando tu información</span
              ></div
            >
            <div class="flex flex-col items-center mt-4 text-center">
              <div>
                <span class="text-sm font-bold text-gray-700"
                  >Este proceso puede tardar unos minutos.</span
                >
              </div>
              <div class="mt-10">
                <GxLoader />
              </div>
              <div class="flex flex-col font-bold w-86">
                <GxProgress
                  :showPercentageProgress="true"
                  :messageProgress="'Recuperando información del archivo'"
                  class="mt-10"
                  ref="gxProgress"
                />
              </div>
              <!-- <span class="mb-10 text-sm ">
                Descripción del elemento migrado
              </span> -->
            </div>
          </div>
          <div v-else class="flex flex-col text-center">
            <div v-if="$mq === 'de'" class="flex-grow text-2xl">
              <span class="font-black"
                >La importación se terminó correctamente</span
              ></div
            >
            <div class="flex flex-col w- 50 items-center mt-10 ">
              <table class="table-fixed text-sm">
                <tbody>
                  <tr>
                    <td class="px-4 py-2 text-left">Cuentas</td>
                    <td class="px-4 py-2 text-right font-bold">{{
                      this.numberOfLedgerAccounts
                    }}</td>
                  </tr>
                  <tr>
                    <td class="px-4 py-2 text-left">Períodos</td>
                    <td class="px-4 py-2 text-right font-bold">{{
                      this.numberOfPeriods
                    }}</td>
                  </tr>
                  <tr>
                    <td class="px-4 py-2 text-left">Pólizas</td>
                    <td class="px-4 py-2 text-right font-bold">{{
                      this.numberOfJournalEntries
                    }}</td>
                  </tr>
                  <tr>
                    <td class="px-4 py-2 text-left">Proveedores</td>
                    <td class="px-4 py-2 text-right font-bold">{{
                      this.numberOfSuppliers
                    }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class=" flex flex-col items-end mt-5">
              <GxButton @click="goToDashboard">INICIAR</GxButton>
            </div>
          </div>
        </div>
      </div>
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Error al continuar con el proceso de migración'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="resetProcess">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </div>
</template>
