<script>
import GxIcon from '@components/_Core/Icons/GxIcon'
import GxUploadFile from '@components/_CoreTaxes/UploadFile/GxUploadFile'

export default {
  name: 'ImportAccountingBackup',
  data() {
    return {}
  },
  components: { GxIcon, GxUploadFile },
  methods: {
    addDragFile(e) {
      this.$refs.gxUploadFile.setFile(e.dataTransfer.files[0])
    },
    sendFile(file) {
      //console.log('entro')
      this.$router.push({
        name: 'uploadAccountingBackup',
        params: { base64: file },
      })
    },
    goToBack() {
      this.$router.push({ name: 'createNewAccounting' })
    },
  },
}
</script>

<template>
  <div
    class="flex flex-col justify-between flex-grow de:p-0 body-gradients font-nunito"
    v-cloak
    @drop.prevent="addDragFile"
    @dragover.prevent
  >
    <div class="flex">
      <div class="mx-auto mt-0 de:mt-8">
        <div
          class="flex flex-col p-8 mt-0 bg-white rounded-lg de:mt-40 w-82 shadow-3 z-1 de:w-200"
        >
          <div v-if="$mq === 'de'" class="text-2xl "
            ><div class="flex flex-grow"
              ><GxIcon
                class="pt-1 mr-5 cursor-pointer"
                :icon="'arrow_back'"
                @click="goToBack"
              />
              <span class="font-black">Importar archivo</span></div
            >
          </div>
          <div
            class="flex justify-between mt-0 mb-0 de:mb-8 de:mt-7"
            :class="{ 'flex-col': $mq === 'mo' }"
          >
            <div class="flex flex-grow inline ml-0 de:ml-7">
              <GxUploadFile
                ref="gxUploadFile"
                :fileType="'respaldo'"
                :extensions="Array('.zip')"
                @sendFile="sendFile"
              />
            </div>
            <div class="flex flex-col inline mt-5 de:mt-0 w-82">
              <div>
                <span class="flex flex-col block text-xs font-bold de:text-sm">
                  Carga tu archivo de COI o CONTPAQi<br />
                  Contabilidad y arranca con taxes de inmediato<br />
                  y justo donde te quedaste.
                </span>
              </div>
              <div class="mt-5 text-2xs de:text-xs">
                <p
                  ><span class="font-bold text-primary">1.- </span
                  ><span>Genera un backup dentro de COI o CONTPAQi</span></p
                >
                <p class="mt-2">
                  <span class="font-bold text-primary">2.- </span
                  ><span
                    >Genera un archivo .ZIP que contenga el respaldo</span
                  ></p
                >
                <p class="mt-2">
                  <span class="font-bold text-primary">3.- </span
                  ><span
                    >Arrastra el archivo aquí o selecciónalo de tu equipo</span
                  ></p
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
