import gql from 'graphql-tag'

export const CREATE_ACCOUNTING_FIRM = gql`
  mutation accountingFirmCreate($companyNumber: ID!) {
    accountingFirmCreate(companyNumber: $companyNumber) {
      accountingFirm {
        accountingFirmNumber
        taxId
        legalName
        commercialName
        countryCode
        creationDate
        logo
        status
        role
      }
      error {
        code
        message
        details
      }
    }
  }
`

export const CREATE_ACCOUNTING_FIRM_CLIENT = gql`
  mutation clientCreate(
    $accountingFirmNumber: ID!
    $input: ClientCreateInput!
  ) {
    clientCreate(accountingFirmNumber: $accountingFirmNumber, input: $input) {
      client {
        clientNumber
        taxId
        legalName
        commercialName
        countryCode
        creationDate
        companyLogo
        favorite
        clientStatus
        accountingFirmClientStatus
        role
      }
      error {
        code
        message
        details
      }
    }
  }
`

