export const LEDGER_ACCOUNT_CREATE = gql`
  mutation ledgerAccountCreate(
    $accountingFirmNumber: ID!
    $clientNumber: ID!
    $input: LedgerAccountCreateInput!
  ) {
    ledgerAccountCreate(
      accountingFirmNumber: $accountingFirmNumber
      clientNumber: $clientNumber
      input: $input
    ) {
      ledgerAccount {
        ledgerAccountId
        number
        shortNumber
        name
        parent
        ledgerAccountType {
          type
          subType
          subSubType
        }
        nature
        ledgerAccountStatus
        additionalDataLocalized {
          ... on LedgerAccountAdditionalDataLocalized_MEX {
            codigoAgrupadorDelSat
          }
        }
      }
      error {
        code
        message
        details
      }
    }
  }
`

