export const JOURNAL_ENTRY_CREATE = gql`
  mutation(
    $accountingFirmNumber: ID!
    $clientNumber: ID!
    $input: JournalEntryCreateInput!
  ) {
    journalEntryCreate(
      accountingFirmNumber: $accountingFirmNumber
      clientNumber: $clientNumber
      input: $input
    ) {
      journalEntry {
        journalEntryId
        date
        description
        number
        totalCredit
        totalDebit
        type
        sourceTransactionType
        sourceTotalAmount
        status
        attachments {
          attachmentId
          urlFile
        }
        entries {
          entryId
          ledgerAccountId
          index
          description
          credit
          debit
          exchangeRate
          attachments {
            attachmentId
            urlFile
          }
          additionalDataLocalized {
            ... on EntryAdditionalDataLocalized_MEX {
              cfdis {
                fecha
                folio
                rfcDelEmisor
                rfcDelReceptor
                tipo
                uuidCfdi
                total
                uuidCfdi
              }
            }
          }
        }
      }
      error {
        code
        message
        details
      }
    }
  }
`

