<script>
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
import { CREATE_ACCOUNTING_FIRM_CLIENT } from '@src/apollo/mutations'
import { GET_ACCOUNTING_FIRMS } from '@src/apollo/queries'

export default {
  name: 'CreateNewAccountingClient',
  props: {
    taxIdProp: {
      type: String,
      default: '',
      required: false,
    },
    legalNameProp: {
      type: String,
      default: '',
      required: false,
    },
    commercialNameProp: {
      type: String,
      default: '',
      required: false,
    },
  },
  components: {
    GxCardModal,
  },
  data() {
    return {
      accountingFirm: null,
      configurationType: '',
      taxId: '',
      legalName: '',
      commercialName: '',
      loading: false,
      errorDescription: '',
      regularRhraseTaxIdPF:
        '^(([A-ZÑ&]{4})([0-9]{2})([0][13578]|[1][02])(([0][1-9]|[12][\\d])|[3][01])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([0-9]{2})([0][13456789]|[1][012])(([0][1-9]|[12][\\d])|[3][0])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([02468][048]|[13579][26])[0][2]([0][1-9]|[12][\\d])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([0-9]{2})[0][2]([0][1-9]|[1][0-9]|[2][0-8])([A-Z0-9]{3}))$',
      regularRhraseTaxIdPM:
        '^(([A-ZÑ&]{4})([0-9]{2})([0][13578]|[1][02])(([0][1-9]|[12][\\d])|[3][01])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([0-9]{2})([0][13456789]|[1][012])(([0][1-9]|[12][\\d])|[3][0])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([02468][048]|[13579][26])[0][2]([0][1-9]|[12][\\d])([A-Z0-9]{3}))|' +
        '(([A-ZÑ&]{4})([0-9]{2})[0][2]([0][1-9]|[1][0-9]|[2][0-8])([A-Z0-9]{3}))$',
    }
  },
  apollo: {
    myAccountingFirms: {
      query: GET_ACCOUNTING_FIRMS,
    },
  },
  computed: {
    labelTypeRFC() {
      return this.taxId.match(this.regularRhraseTaxIdPF) !== null
        ? 'Nombre del Contribuyente'
        : 'Razón Social'
    },
  },
  watch: {
    myAccountingFirms: {
      handler(newVal) {
        if (newVal) {
          this.accountingFirm = newVal[0]
          //console.log(newVal)
        }
      },
    },
  },
  methods: {
    async createClient() {
      if (!localStorage.getItem('companyNumber')) {
        try {
          const response = await this.$apollo.mutate({
            mutation: CREATE_ACCOUNTING_FIRM_CLIENT,
            variables: {
              accountingFirmNumber: this.accountingFirm.accountingFirmNumber,
              input: {
                taxId: this.taxId,
                legalName: this.legalName,
                commercialName: this.commercialName,
                countryCode: 'MEX',
                currency: 'MXN',
              },
            },
          })

          if (
            response.data.clientCreate.client === null &&
            response.data.clientCreate.error != null
          ) {
            this.errorDescription =
              response.data.clientCreate.error.message +
              ' - ' +
              response.data.clientCreate.error.details.join('')
          } else {
            localStorage.setItem(
              'companyNumber',
              response.data.clientCreate.client.clientNumber
            )
            localStorage.setItem(
              'legalName',
              response.data.clientCreate.client.legalName
            )
            localStorage.setItem(
              'taxId',
              response.data.clientCreate.client.taxId
            )

            localStorage.setItem('migration_tax_id', this.taxId)
            localStorage.setItem('migration_tax_id', this.taxId)

            if (localStorage.getItem('configurationType') === 'migration') {
              this.$router.push({ name: 'progressMigrationBackupView' })
            } else {
              this.$router.push({ name: 'templates' })
            }
          }
        } catch (error) {
          this.errorDescription = this.$utils.parseServerError(error)
          return null
        }
      } else {
        if (localStorage.getItem('configurationType') === 'migration') {
          this.$router.push({ name: 'progressMigrationBackupView' })
        } else {
          this.$router.push({ name: 'templates' })
        }
      }
    },
    goToBack() {
      this.$router.push({ name: 'createNewAccounting' })
    },
    closeModel() {
      this.errorDescription = ''
    },
  },
  mounted() {
    if (localStorage.getItem('migration_tax_id')) {
      this.taxId = localStorage.getItem('migration_tax_id')
    }
    if (localStorage.getItem('migration_business_name')) {
      this.taxId = localStorage.getItem('migration_business_name')
    }
    this.configurationType = localStorage.getItem('configurationType')
  },
  clearItemsLocalStorage() {
    localStorage.removeItem('migration_tax_id')
    localStorage.removeItem('migration_business_name')
    localStorage.removeItem('configurationType')
  },
}
</script>

<template>
  <div
    class="flex flex-col justify-between flex-grow de:p-0 body-gradients font-nunito"
    v-cloak
    @drop.prevent="addDragFile"
    @dragover.prevent
  >
    <div class="flex flex-col">
      <div class="mx-auto mt-0 de:mt-8">
        <div
          class="flex flex-col p-8 mt-0 bg-white rounded-lg de:mt-40 w-82 shadow-3 z-1 de:w-200"
        >
          <div v-if="$mq === 'de'" class="text-2xl "
            ><div class="flex flex-grow"
              ><GxIcon
                class="pt-1 mr-5 cursor-pointer"
                :icon="'arrow_back'"
                @click="goToBack"
              />
              <span class="font-black">Datos Generales</span></div
            >
          </div>
          <div class="flex flex-col justify-between">
            <div class="flex flex-col items-center">
              <div class="w-100 mt-15">
                <GxTextFieldTaxId
                  ref="taxId"
                  v-model="taxId"
                  required
                  label="RFC *"
                  name="RFC"
                  leftIcon="business"
                  placeholder="RFC *"
                  autofocus
                  class="mb-2"
                />
                <GxTextFieldBusinessName
                  v-model="legalName"
                  :label="labelTypeRFC"
                  :placeholder="labelTypeRFC"
                  name="Razón Social"
                  leftIcon="business_center"
                  class="mb-2"
                />
                <GxTextFieldBusinessName
                  v-model="commercialName"
                  label="Nombre Comercial"
                  placeholder="Nombre Comercial"
                  name="Nombre Comercial"
                  leftIcon="store"
                  class="mb-2"
                />
              </div>
            </div>
            <div class="flex flex-col items-end mt-15">
              <GxButton
                class="flex flex-col items-end "
                @click="createClient"
                :loading="loading"
                :disabled="loading"
                >SIGUIENTE</GxButton
              >
            </div>
          </div>
        </div>
      </div>
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Error al tratar de crear al cliente'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModel">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </div>
</template>
