<script>
export default {
  name: 'GxProgress',
  props: {
    showPercentageProgress: {
      type: Boolean,
      default: false,
    },
    messageProgress: {
      type: String,
      default: ',',
      required: false,
    },
  },
  data() {
    return {
      progress: 0,
      text: this.messageProgress,
    }
  },
  methods: {
    changeProgress(progress) {
      this.progress = progress
    },
    changeProgressAndElement(progress, text) {
      this.progress = progress
      this.text = text
    },
  },
}
</script>
<template>
  <div class="flex flex-col w-full text-center">
    <div class="flex flex-col w-full h-4 text-center progress ">
      <!-- v-if="showPercentageProgress" -->

      <div
        class="flex flex-col rounded-l-lg progress-bar progress-bar-disable-effect bg-primary"
        :class="{ 'rounded-r-lg': progress > 95 }"
        role="progressbar"
        v-bind:style="{ width: progress + '%' }"
      >
        <span
          class="flex flex-col block w-full text-center text-primary text-2xs h-4"
        >
        </span>
      </div>
      <div class="flex flex-col relative block"
        ><span
          v-if="showPercentageProgress"
          class="flex flex-col block w-full text-center text-black text-xs font-bold"
          >{{ progress }}%</span
        >
      </div>
    </div>
    <div v-if="text" class="mt-5 text-sm">{{ text }}</div>
  </div>
</template>
