<script>
import GxTextField from './GxTextField'
import { Datetime } from 'vue-datetime'
import { Settings } from 'luxon'
// https://date-fns.org/docs/format
import format from 'date-fns/format'
const es = require('date-fns/locale/es')
Settings.defaultLocale = 'es'
export default {
  name: 'GxTextFieldDatePicker',
  components: {
    GxTextField,
    Datetime,
  },
  props: {
    label: {
      type: String,
      default: 'Input',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    minDatetime: {
      type: String,
      default: null,
    },
    maxDatetime: {
      type: String,
      default: null,
    },
    format: {
      type: String,
      default: 'D [de] MMMM [de] YYYY',
    },
    value: {
      type: String,
      required: true,
    },
    icon: {
      type: String,
      default: 'calendar_today',
    },
  },
  data() {
    return {
      selectedDate: this.value || '',
    }
  },
  computed: {
    formattedDate() {
      if (!this.selectedDate) return ''
      return format(new Date(this.selectedDate), this.format, {
        locale: es,
      })
    },
  },
  watch: {
    value(newVal) {
      this.selectedDate = newVal
    },
    formattedDate(newVal) {
      this.$emit('input', this.selectedDate)
    },
  },
  methods: {
    onFocus() {
      if (this.disabled) {
        this.$refs.GxTextField.blur()
        return
      }
      this.$refs.datetime.open({
        target: {
          blur: function() {},
        },
      })
    },
    onBlur() {
      this.showCalendar = false
    },
  },
}
</script>
<template>
  <div>
    <GxTextField
      ref="GxTextField"
      :value="formattedDate"
      readonly
      :label="label"
      @focus="onFocus"
    />
    <Datetime
      ref="datetime"
      v-model="selectedDate"
      type="date"
      zone="local"
      value-zone="local"
      :week-start="7"
      :min-datetime="minDatetime"
      :max-datetime="maxDatetime"
      :phrases="{ ok: 'OK', cancel: 'Cancelar' }"
      class="hideVCalendarInput"
    />
  </div>
</template>
<style lang="scss">
// Hide Calendar default input
// Can't actually hide since it's needed to open calendar popup
.hideVCalendarInput {
  position: relative;
  overflow: hidden;
}
.hideVCalendarInput > input {
  position: absolute;
  left: -100%;
}
// Overwrite VCalendar styles
.vdatetime-popup__header {
  display: none;
}
.vdatetime-popup {
  border-radius: 0.5rem;
}
/* stylelint-disable */
.vdatetime-popup__actions,
.vdatetime-calendar__month__day,
.vdatetime-calendar__month__weekday {
  font-family: 'Muli';
}
/* stylelint-enable */
.vdatetime-calendar__month__day--selected > span > span,
.vdatetime-calendar__month__day--selected:hover > span > span {
  background-color: var(--secondary-300);
}
.vdatetime-calendar__month__weekday {
  visibility: hidden;
}
.vdatetime-calendar__month__weekday::first-letter {
  padding-left: 15px;
  visibility: visible;
}
.vdatetime-calendar__current--month {
  text-transform: lowercase;
}
/* stylelint-disable */
.vdatetime-calendar__navigation * {
  font-family: 'Muli';
  font-size: 24px;
  font-weight: 600;
  line-height: 12px;
  letter-spacing: 0;
}
/* stylelint-enable */
</style>
