<script>
import GxIcon from '@components/_Core/Icons/GxIcon'
import GxProgress from '@components/_CoreTaxes/Progress/GxProgress'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
import GxLoader from '@components/_Core/Utils/GxLoader'
import { axiosGxYakBackMigrations } from '@src/axiosConfig'

export default {
  name: 'ImportAccountingBackup',
  data() {
    return {
      progress: 0,
      errorDescription: null,
      RMPostMigration: {
        migrationId: '',
        userNumber: 0,
        originUploadBackup: '',
        data: '',
      },
      validatingFile: false,
    }
  },
  props: {
    base64: {
      type: String,
      default: '',
      required: false,
    },
  },
  components: { GxIcon, GxProgress, GxCardModal, GxLoader },
  mounted() {
    const timeout = setTimeout(() => {
      this.PostBackupFile()
    }, 1000)
    this.$once('hook:destroyed', () => {
      clearTimeout(timeout)
    })
  },
  methods: {
    addDragFile(e) {
      this.$refs.gxUploadFile.setFile(e.dataTransfer.files[0])
    },
    generateGuid() {
      return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) =>
        (
          c ^
          (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
        ).toString(16)
      )
    },
    goToBack() {
      this.$router.push({ name: 'importAccountingBackup' })
    },
    onProgress(progress) {
      this.$refs.gxProgress.changeProgress(progress)
      if (progress == 100) {
        const timeout = setTimeout(() => {
          this.validatingFile = true
        }, 500)
        this.$once('hook:destroyed', () => {
          clearTimeout(timeout)
        })
      }
    },
    PostBackupFile() {
      this.RMPostMigration.migrationId = this.generateGuid()
      this.RMPostMigration.data = this.base64
      this.RMPostMigration.originUploadBackup = 'yakWeb'
      this.RMPostMigration.userNumber = Number(
        localStorage.getItem('userNumber')
      )

      let config = {
        onUploadProgress: (progressEvent) => {
          this.onProgress(
            Math.floor((progressEvent.loaded * 100) / progressEvent.total)
          )
        },
      }
      localStorage.setItem('migrationId', this.RMPostMigration.migrationId)
      axiosGxYakBackMigrations
        .post(`/api/v1/migrations`, this.RMPostMigration, config)
        .then((res) => {
          console.log('exito')
          console.log(res.data)
          localStorage.setItem(
            'migration_tax_id',
            res.data.taxId === null ? '' : res.data.taxId
          )
          localStorage.setItem(
            'migration_business_name',
            res.data.businessName === null ? '' : res.data.businessName
          )
          this.$router.push({ name: 'createNewAccountingClientView' })
        })
        .catch((err) => {
          //this.loading = false
          this.errorDescription = this.$utils.parseServerError(err)
          console.log(this.errorDescription)
        })
    },
  },
}
</script>

<template>
  <div
    class="flex flex-col justify-between flex-grow de:p-0 body-gradients font-nunito"
    v-cloak
    @drop.prevent="addDragFile"
    @dragover.prevent
  >
    <div class="flex flex-col">
      <div class="mx-auto mt-0 de:mt-8">
        <div
          class="flex flex-col p-8 mt-0 bg-white rounded-lg de:mt-40 w-82 shadow-3 z-1 de:w-200"
        >
          <div v-if="$mq === 'de'" class="text-2xl "
            ><div class="flex flex-grow"
              ><GxIcon
                class="pt-1 mr-5 cursor-pointer"
                :icon="'arrow_back'"
                @click="goToBack"
              />
              <span class="font-black">Importar archivo</span></div
            >
          </div>
          <div
            class="flex flex-col items-center justify-between w-full mt-0 mb-0 de:mb-8 de:mt-7"
            :class="{ 'flex-col': $mq === 'mo' }"
          >
            <div class="flex items-center mb-5">
              <div
                v-if="!validatingFile"
                class="pl-6 pr-6 border rounded-lg w-86"
              >
                <GxProgress
                  :showPercentageProgress="true"
                  :messageProgress="'Cargando tu respaldo'"
                  class="mt-30 mb-30"
                  ref="gxProgress"
                />
              </div>
              <div
                v-else
                class="flex flex-col items-center pl-6 pr-6 border rounded-lg w-86"
              >
                <GxLoader class="mt-20 mb-5" />
                <span class="flex text-xs mb-31 de:text-sm"
                  >Validando tu respaldo
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Error al importar el archivo'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="goToBack">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </div>
</template>
